  /* Root Variables (copied for compatibility) */
  :root {
    --bg: #ffffff;
    --text: #111827;
    --muted: #6b7280;
    --primary: #16a34a;
    --border: #e5e7eb;
    --radius: 14px;
    --shadow: 0 2px 8px rgba(17, 24, 39, 0.06);
  }

  /* Chat Scroll Wrapper */
  .chat-scroll-wrapper {
    flex: 1;
    overflow-y: hidden;
    display: flex;
    justify-content: center;
    scroll-behavior: smooth;
    padding-bottom: 120px;
  }

  .chat-scroll-wrapper.has-messages {
    overflow-y: auto;
  }

  .chat-scroll-wrapper::-webkit-scrollbar {
    width: 8px;
  }

  .chat-scroll-wrapper::-webkit-scrollbar-track {
    background: transparent;
  }

  .chat-scroll-wrapper::-webkit-scrollbar-thumb {
    background: #cbd5e1;
    border-radius: 4px;
  }

  .chat-scroll-wrapper::-webkit-scrollbar-thumb:hover {
    background: #94a3b8;
  }

  /* Chat Container */
  .chat-container {
    width: 100%;
    max-width: 800px;
    /* ✅ nhỏ hơn, canh giữa */
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin: 0 auto;
    padding: 0 8px;
    padding-bottom: 140px !important;
    /* tăng khoảng đệm cho vùng chat */

  }

  /* Chat Inner */
  .chat-inner {
    width: 100%;
    max-width: 940px;
    padding: 0;
    display: flex;
    flex-direction: column;
    gap: 12px;
    min-height: calc(100% - 64px);
  }

  /* Chat Message */
  .chat-message {
    padding: 10px 14px;
    border-radius: 18px;
    /* ✅ bo tròn nhẹ hơn */
    max-width: 80%;
    /* ✅ thu nhỏ khung lại */
    word-wrap: break-word;
    box-shadow: none;
  }

  .chat-message.user {
    align-self: flex-end;
    background: #f5f5f5;
    /* ✅ xám nhẹ hơn, dịu mắt như ChatGPT */
    color: #111827;
    /* ✅ chữ đậm, dễ đọc */
    border-radius: 22px;
    /* ✅ bo tròn đều, không còn góc nhọn */
    padding: 10px 14px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
    /* ✅ bóng nhẹ tinh tế */
  }


  .chat-message pre,
  .chat-message code {
    border-radius: 8px;
    overflow-x: auto;
  }

  .chat-message.ai {
    align-self: flex-start;
    background: #f9fafb;
    border-radius: 18px 18px 18px 4px;
    color: var(--text);
    box-shadow: var(--shadow);
  }

  /* Message Box */
  .message-box {
    display: flex;
    flex-direction: column;
    gap: 8px;
    text-align: left;
  }

  /* Markdown Support */
  .chat-message p {
    margin: 4px 0;
    text-align: left;
  }

  .chat-message strong {
    font-weight: 700;
  }

  .chat-message em {
    font-style: italic;
  }

  .chat-message a {
    color: var(--brand);
    text-decoration: underline;
  }

  .chat-message blockquote {
    border-left: 4px solid var(--primary);
    background: #ecfdf5;
    padding: 8px 12px;
    margin: 8px 0;
    font-style: italic;
  }

  /* Code Blocks */
  .chat-message pre {
    background: #1e293b;
    color: #f8fafc;
    font-family: 'Fira Code', monospace;
    border-radius: 8px;
    padding: 12px 16px;
    overflow-x: auto;
    margin: 8px 0;
    position: relative;
  }

  .chat-message code {
    background: #e2e8f0;
    color: #0f172a;
    padding: 2px 6px;
    border-radius: 4px;
    font-family: 'Fira Code', monospace;
    font-size: 14px;
  }

  /* Code Block Wrapper */
  .code-block-wrapper {
    position: relative;
  }

  .code-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 6px 12px;
    background: #2d3748;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    font-size: 13px;
    color: #e5e7eb;
  }

  .code-lang {
    display: flex;
    align-items: center;
    gap: 6px;
  }

  .code-block {
    margin: 0;
  }

  /* Copy Button */
  .copy-button {
    background: #4b5563;
    color: #fff;
    border: none;
    border-radius: 6px;
    padding: 4px 8px;
    font-size: 12px;
    cursor: pointer;
    transition: background 0.2s;
  }

  .copy-button:hover {
    background: #6b7280;
  }

  .copy-button.copied {
    background: var(--primary);
  }

  /* Tables */
  .chat-message table {
    width: 100%;
    max-width: 100%;
    border-collapse: collapse;
    margin: 10px 0;
  }

  .chat-message th,
  .chat-message td {
    border: 1px solid var(--border);
    padding: 8px 12px;
    text-align: left;
  }

  .chat-message th {
    background: #e5e7eb;
    font-weight: 600;
  }

  .chat-message td {
    background: var(--bg);
  }

  /* Images in Chat */
  .chat-message img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
    margin: 8px 0;
  }

  /* Feedback Bar */
  .feedback-bar {
    display: flex;
    justify-content: flex-end;
    gap: 8px;
    margin-top: 4px;
  }

  .btn-icon {
    background: transparent;
    border: none;
    font-size: 16px;
    cursor: pointer;
    color: var(--muted);
    transition: color 0.2s;
  }

  .btn-icon:hover {
    color: var(--primary);
  }

  /* Typing Indicator */
  /* Hiển thị chấm "đang suy nghĩ" kiểu ChatGPT */
  .typing-indicator {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: transparent;
    /* Không có khung nền */
    width: fit-content;
    margin: 6px 0;
    min-height: 20px;
  }

  /* Chấm đen rõ ràng */
  .typing-indicator span {
    width: 10px;
    height: 10px;
    background: #000;
    /* ✅ Đen hẳn, không xám nữa */
    border-radius: 50%;
    opacity: 0.6;
    animation: thinkingPulse 1.2s infinite ease-in-out;
  }

  /* Hiệu ứng nhịp nhẹ nhàng */
  @keyframes thinkingPulse {

    0%,
    100% {
      opacity: 0.4;
      transform: scale(1);
    }

    50% {
      opacity: 1;
      transform: scale(1.5);
    }
  }

  /* Khi biến mất (khi AI trả lời xong) */
  @keyframes fadeOutWhenDone {
    from {
      opacity: 1;
      transform: scale(1);
    }

    to {
      opacity: 0;
      transform: scale(0.7);
    }
  }


  /* Navigation Buttons */
  .nav-btn {
    width: 40px;
    height: 40px;
    border-radius: 999px;
    border: 1px solid var(--border);
    background: rgba(255, 255, 255, 0.8);
    display: grid;
    place-items: center;
    cursor: pointer;
    color: var(--text);
    font-size: 18px;
    transition: all 0.3s ease;
    box-shadow: var(--shadow);
  }

  .nav-btn:hover {
    background: var(--paper);
    transform: translateY(-2px);
    box-shadow: 0 6px 16px rgba(17, 24, 39, 0.08);
  }

  .nav-btn:active {
    transform: scale(0.95);
  }

  /* Pill */
  .pill {
    display: flex;
    align-items: center;
    gap: 12px;
    background: rgba(255, 255, 255, 0.8);
    border: 1px solid var(--border);
    border-radius: 999px;
    padding: 12px 20px;
    box-shadow: var(--shadow);
    font-size: 16px;
    color: var(--text);
    font-family: 'Inter', system-ui;
    font-weight: 500;
    max-width: 600px;
    animation: fadeIn 0.5s ease;
  }

  .pill .thumb {
    font-size: 24px;
  }

  /* Explore */
  .explore {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 12px;
    margin-top: 24px;
    animation: slideIn 0.8s ease-out 0.3s both;
  }

  /* Tag */
  .tag {
    padding: 10px 16px;
    border: 1px solid var(--border);
    border-radius: 999px;
    background: rgba(255, 255, 255, 0.8);
    color: var(--text);
    font-family: 'Inter', system-ui;
    font-weight: 600;
    font-size: 14px;
    display: flex;
    align-items: center;
    gap: 8px;
    transition: all 0.3s ease;
    box-shadow: var(--shadow);
  }

  .tag:hover {
    background: var(--paper);
    transform: translateY(-2px);
    box-shadow: 0 6px 16px rgba(17, 24, 39, 0.08);
  }

  .tag:active {
    transform: translateY(0);
  }

  /* Tag Icons */
  .tag-icon {
    font-size: 18px;
    transition: all 0.3s ease;
  }

  .ask-icon {
    color: var(--brand);
  }

  .tag:hover .ask-icon {
    animation: spin 2s linear infinite;
    background: var(--gradient);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
  }

  .explore-icon {
    color: var(--primary);
  }

  .tag:hover .explore-icon {
    animation: wobble 1.5s ease-in-out infinite;
    background: var(--gradient);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
  }

  .chat-icon {
    color: #22c55e;
  }

  .tag:hover .chat-icon {
    animation: pulse 2s ease-in-out infinite;
    background: var(--gradient);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
  }

  .learn-icon {
    color: var(--brand);
  }

  .tag:hover .learn-icon {
    animation: bounce 1.8s ease-in-out infinite;
    background: var(--gradient);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
  }

  /* Animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }

  @keyframes typingDot {

    0%,
    80%,
    100% {
      opacity: 0.3;
      transform: scale(1);
    }

    40% {
      opacity: 1;
      transform: scale(1.2);
    }
  }

  @keyframes spin {
    from {
      transform: rotate(0deg);
    }

    to {
      transform: rotate(360deg);
    }
  }

  @keyframes wobble {

    0%,
    100% {
      transform: rotate(0deg);
    }

    25% {
      transform: rotate(12deg);
    }

    75% {
      transform: rotate(-12deg);
    }
  }

  @keyframes bounce {

    0%,
    100% {
      transform: translateY(0);
    }

    50% {
      transform: translateY(-8px);
    }
  }

  /* Main */
  .main {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background: var(--bg);
    overflow: hidden;
    padding-bottom: 25px;
    /* 👈 điều chỉnh tùy theo chiều cao thật của .input-area */

  }

  /* Hero */
  .hero {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    width: 100%;
    padding: 0;
    margin: 0;
  }

  /* Input Area */
  .input-area {
    position: sticky;
    bottom: 0;
    background: var(--bg);
    padding: 10px 0 26px;
    /* 👈 thêm khoảng đệm dưới để chat không bị che */
    z-index: 10;
    width: 100%;
    max-width: 940px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .composer {
    display: flex;
    align-items: center;
    gap: 8px;
    width: 100%;
    max-width: 940px;
    background: #fff;
    border: 1px solid var(--border);
    border-radius: 24px;
    padding: 10px 16px;
    box-shadow: var(--shadow);
  }

  /* Composer */
  .composer.grok-style {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 6px;
    background: #fff;
    border: 1px solid #d1d5db;
    border-radius: 999px;
    /* Bo tròn hoàn toàn */
    padding: 4px 8px;
    /* 👈 giảm padding → mỏng hơn */
    width: 90%;
    max-width: 800px;
    margin: 8px auto;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
    transition: box-shadow 0.2s ease;
    min-height: 42px;
    /* 👈 thấp hơn khung cũ */
  }

  .composer.grok-style textarea {
    flex: 1;
    border: none;
    outline: none;
    font-size: 15px;
    color: var(--text);
    resize: none;
    min-height: 20px;
    /* 👈 nhỏ hơn chút */
    max-height: 100px;
    /* 👈 vừa đủ để mở rộng */
    overflow-y: auto;
    padding: 11px 12px 0px;
    border-radius: 999px;
    line-height: 1.4;
    background: transparent;
    vertical-align: middle;
  }

  .composer.grok-style textarea::placeholder {
    color: var(--muted);
  }

  .composer.grok-style .send-btn {
    width: 34px;
    height: 34px;
    border-radius: 50%;
    background: #111;
    /* đen giống ChatGPT */
    color: white;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.2s ease;
    margin-right: 4px;
    /* Dịch nhẹ sang trái */
  }

  .composer.grok-style .send-btn:hover {
    background: #27272a;
    /* Hover sáng hơn nhẹ */
    transform: translateY(-1px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
  }

  .composer.grok-style .send-btn:active {
    transform: scale(0.96);
    background: #000;
  }

  .composer.grok-style .send-btn svg {
    width: 18px;
    height: 18px;
    fill: currentColor;
  }